"use client"

import { useState } from "react"
import { Dumbbell, ChevronDown, ChevronUp, Star } from "lucide-react"
import type { Exercise, MuscleGroup } from "@/lib/exercise-data"
import { muscleLabels } from "@/lib/exercise-data"
import { cn } from "@/lib/utils"
import { Badge } from "@/components/ui/badge"

interface ExerciseLogProps {
  exercises: Exercise[]
  onHoverMuscle?: (muscle: MuscleGroup | null) => void
}

export function ExerciseLog({ exercises, onHoverMuscle }: ExerciseLogProps) {
  const [expandedId, setExpandedId] = useState<string | null>(null)

  // Group exercises by date
  const grouped = exercises.reduce<Record<string, Exercise[]>>((acc, ex) => {
    if (!acc[ex.date]) acc[ex.date] = []
    acc[ex.date].push(ex)
    return acc
  }, {})

  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr + "T12:00:00")
    const today = new Date("2026-02-06T12:00:00")
    const yesterday = new Date("2026-02-05T12:00:00")

    if (date.toDateString() === today.toDateString()) return "Today"
    if (date.toDateString() === yesterday.toDateString()) return "Yesterday"
    return date.toLocaleDateString("en-US", { weekday: "short", month: "short", day: "numeric" })
  }

  const getPointsColor = (points: number) => {
    if (points >= 400) return "text-accent"
    if (points >= 250) return "text-primary"
    return "text-muted-foreground"
  }

  return (
    <div className="space-y-5">
      {Object.entries(grouped).map(([date, dayExercises]) => {
        const dayTotal = dayExercises.reduce((sum, ex) => sum + ex.points, 0)
        return (
          <div key={date} className="space-y-2">
            <div className="flex items-center justify-between">
              <h3 className="text-sm font-semibold text-foreground">
                {formatDate(date)}
              </h3>
              <span className="flex items-center gap-1 text-xs font-mono text-primary">
                <Star className="h-3 w-3" />
                {dayTotal.toLocaleString()} XP
              </span>
            </div>

            <div className="space-y-1.5">
              {dayExercises.map((exercise) => {
                const isExpanded = expandedId === exercise.id

                return (
                  <div
                    key={exercise.id}
                    className={cn(
                      "group rounded-lg border border-border bg-card/50 transition-all hover:bg-card",
                      isExpanded && "bg-card border-primary/20"
                    )}
                  >
                    <button
                      type="button"
                      className="flex w-full items-center gap-3 p-3"
                      onClick={() => setExpandedId(isExpanded ? null : exercise.id)}
                      onMouseEnter={() => {
                        if (onHoverMuscle && exercise.musclesWorked.length > 0) {
                          onHoverMuscle(exercise.musclesWorked[0])
                        }
                      }}
                      onMouseLeave={() => onHoverMuscle?.(null)}
                    >
                      <div className="flex h-9 w-9 shrink-0 items-center justify-center rounded-lg bg-secondary">
                        <Dumbbell className="h-4 w-4 text-muted-foreground" />
                      </div>

                      <div className="flex-1 text-left">
                        <p className="text-sm font-medium text-foreground">
                          {exercise.name}
                        </p>
                        <p className="text-xs text-muted-foreground">
                          {exercise.sets} sets x {exercise.reps} reps
                          {exercise.weight > 0 && ` @ ${exercise.weight} lbs`}
                        </p>
                      </div>

                      <div className="flex items-center gap-2">
                        <span className={cn("text-sm font-bold font-mono", getPointsColor(exercise.points))}>
                          +{exercise.points}
                        </span>
                        {isExpanded ? (
                          <ChevronUp className="h-4 w-4 text-muted-foreground" />
                        ) : (
                          <ChevronDown className="h-4 w-4 text-muted-foreground" />
                        )}
                      </div>
                    </button>

                    {isExpanded && (
                      <div className="border-t border-border px-3 pb-3 pt-2">
                        <p className="mb-2 text-xs font-medium text-muted-foreground">
                          Muscles Targeted
                        </p>
                        <div className="flex flex-wrap gap-1.5">
                          {exercise.musclesWorked.map((muscle) => (
                            <Badge
                              key={muscle}
                              variant="secondary"
                              className="cursor-default text-xs"
                              onMouseEnter={() => onHoverMuscle?.(muscle)}
                              onMouseLeave={() => onHoverMuscle?.(null)}
                            >
                              {muscleLabels[muscle]}
                            </Badge>
                          ))}
                        </div>
                      </div>
                    )}
                  </div>
                )
              })}
            </div>
          </div>
        )
      })}
    </div>
  )
}
