"use client"

import type { MuscleGroup } from "@/lib/exercise-data"
import { muscleLabels } from "@/lib/exercise-data"

interface MuscleBreakdownProps {
  activation: Record<MuscleGroup, number>
}

function getBarColor(intensity: number): string {
  if (intensity === 0) return "bg-secondary"
  if (intensity < 20) return "bg-emerald-800"
  if (intensity < 40) return "bg-emerald-600"
  if (intensity < 60) return "bg-lime-500"
  if (intensity < 80) return "bg-amber-500"
  return "bg-red-500"
}

export function MuscleBreakdown({ activation }: MuscleBreakdownProps) {
  const sorted = (Object.entries(activation) as [MuscleGroup, number][])
    .filter(([, val]) => val > 0)
    .sort((a, b) => b[1] - a[1])

  if (sorted.length === 0) {
    return (
      <p className="text-sm text-muted-foreground">No muscles activated yet.</p>
    )
  }

  return (
    <div className="space-y-2.5">
      {sorted.map(([muscle, intensity]) => (
        <div key={muscle} className="space-y-1">
          <div className="flex items-center justify-between">
            <span className="text-xs font-medium text-foreground">
              {muscleLabels[muscle]}
            </span>
            <span className="text-xs font-mono text-muted-foreground">
              {intensity}%
            </span>
          </div>
          <div className="h-2 w-full overflow-hidden rounded-full bg-secondary">
            <div
              className={`h-full rounded-full transition-all duration-500 ${getBarColor(intensity)}`}
              style={{ width: `${intensity}%` }}
            />
          </div>
        </div>
      ))}
    </div>
  )
}
