"use client"

import React from "react"

import { Flame, Trophy, Zap, Target } from "lucide-react"
import type { UserStats } from "@/lib/exercise-data"

interface StatsHeaderProps {
  stats: UserStats
}

export function StatsHeader({ stats }: StatsHeaderProps) {
  const xpPercent = (stats.currentXP / stats.nextLevelXP) * 100

  return (
    <div className="space-y-6">
      {/* Level & XP Bar */}
      <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
        <div className="flex items-center gap-4">
          <div className="flex h-14 w-14 items-center justify-center rounded-xl bg-primary/10 ring-2 ring-primary/30">
            <span className="text-xl font-bold text-primary font-mono">
              {stats.level}
            </span>
          </div>
          <div>
            <h1 className="text-xl font-bold text-foreground sm:text-2xl">
              Level {stats.level} Warrior
            </h1>
            <p className="text-sm text-muted-foreground">
              {stats.currentXP.toLocaleString()} / {stats.nextLevelXP.toLocaleString()} XP to next level
            </p>
          </div>
        </div>
        <div className="flex items-center gap-2 rounded-lg bg-accent/10 px-3 py-1.5 ring-1 ring-accent/30">
          <Trophy className="h-4 w-4 text-accent" />
          <span className="text-sm font-semibold text-accent">{stats.rank} Rank</span>
        </div>
      </div>

      {/* XP Progress Bar */}
      <div className="space-y-1.5">
        <div className="h-3 w-full overflow-hidden rounded-full bg-secondary">
          <div
            className="h-full rounded-full bg-primary transition-all duration-700 ease-out"
            style={{
              width: `${xpPercent}%`,
              boxShadow: "0 0 12px hsla(142, 70%, 45%, 0.5)",
            }}
          />
        </div>
        <div className="flex justify-between text-xs text-muted-foreground">
          <span>{xpPercent.toFixed(0)}% complete</span>
          <span>{(stats.nextLevelXP - stats.currentXP).toLocaleString()} XP remaining</span>
        </div>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-2 gap-3 sm:grid-cols-4">
        <StatCard
          icon={<Zap className="h-4 w-4" />}
          label="Total XP"
          value={stats.totalPoints.toLocaleString()}
          color="text-primary"
          bgColor="bg-primary/10"
        />
        <StatCard
          icon={<Flame className="h-4 w-4" />}
          label="Day Streak"
          value={stats.streak.toString()}
          color="text-accent"
          bgColor="bg-accent/10"
        />
        <StatCard
          icon={<Target className="h-4 w-4" />}
          label="Workouts"
          value={stats.totalWorkouts.toString()}
          color="text-chart-3"
          bgColor="bg-chart-3/10"
        />
        <StatCard
          icon={<Trophy className="h-4 w-4" />}
          label="Today's XP"
          value="1,100"
          color="text-accent"
          bgColor="bg-accent/10"
        />
      </div>
    </div>
  )
}

function StatCard({
  icon,
  label,
  value,
  color,
  bgColor,
}: {
  icon: React.ReactNode
  label: string
  value: string
  color: string
  bgColor: string
}) {
  return (
    <div className="rounded-xl border border-border bg-card p-3">
      <div className="flex items-center gap-2">
        <div className={`flex h-7 w-7 items-center justify-center rounded-lg ${bgColor} ${color}`}>
          {icon}
        </div>
        <span className="text-xs text-muted-foreground">{label}</span>
      </div>
      <p className={`mt-2 text-lg font-bold font-mono ${color}`}>{value}</p>
    </div>
  )
}
