"use client"

const weekData = [
  { day: "Mon", xp: 680 },
  { day: "Tue", xp: 920 },
  { day: "Wed", xp: 0 },
  { day: "Thu", xp: 1100 },
  { day: "Fri", xp: 850 },
  { day: "Sat", xp: 690 },
  { day: "Sun", xp: 1100 },
]

export function WeeklyActivity() {
  const maxXP = Math.max(...weekData.map((d) => d.xp))

  return (
    <div className="space-y-3">
      <div className="flex items-end justify-between gap-2">
        {weekData.map((item) => {
          const height = maxXP > 0 ? (item.xp / maxXP) * 100 : 0
          const isToday = item.day === "Sun"

          return (
            <div key={item.day} className="flex flex-1 flex-col items-center gap-1.5">
              <span className="text-[10px] font-mono text-muted-foreground">
                {item.xp > 0 ? item.xp : "-"}
              </span>
              <div className="relative h-24 w-full max-w-[32px]">
                <div className="absolute bottom-0 h-full w-full rounded-md bg-secondary" />
                <div
                  className="absolute bottom-0 w-full rounded-md transition-all duration-500"
                  style={{
                    height: `${height}%`,
                    backgroundColor: isToday
                      ? "hsl(142, 70%, 45%)"
                      : item.xp > 0
                        ? "hsl(142, 50%, 30%)"
                        : "transparent",
                    boxShadow: isToday
                      ? "0 0 8px hsla(142, 70%, 45%, 0.4)"
                      : "none",
                  }}
                />
              </div>
              <span
                className={`text-xs font-medium ${
                  isToday ? "text-primary" : "text-muted-foreground"
                }`}
              >
                {item.day}
              </span>
            </div>
          )
        })}
      </div>
      <div className="flex items-center justify-between text-xs text-muted-foreground">
        <span>Weekly Total</span>
        <span className="font-mono font-semibold text-primary">
          {weekData.reduce((sum, d) => sum + d.xp, 0).toLocaleString()} XP
        </span>
      </div>
    </div>
  )
}
